<template>
    <div class="info-div" style="height: 100%;">
      <div class="info-header">
        <span class="callback" @click="resetComponent">电价方案 </span>
        <span> > 方案详情</span>
      </div>
        <VuePerfectScrollbar class="info-container" v-show="show">
            <div class="list-content detail-content list-contentB">
                <div class="list-header">
                  <i class="hasIcon hasImg">
                    <img src="dist/img/costControl/user-infor-icon.png" />
                  </i>方案信息
                  <!--<button  v-show="formEditState"  class="list-operate" @click="submit" >-->
                    <!--<i class="hasIcon"><icon name="save"></icon></i> 提交-->
                  <!--</button>-->
                  <!--<button  v-if=""  class="list-operate" @click="openEditClick" >-->
                    <!--<i class="hasIcon"><icon name="edit"></icon></i>-->
                    <!--{{formEditState ? '取消' : '编辑'}}-->
                  <!--</button>-->
                </div>
                <ul class="form-list detail-list">
                    <li v-for="(item, $index) in list" v-if="item.isView">
                        <span class="label-show label-span"><span v-if="item.isNotNull"> * </span>{{item.name}} :&nbsp; </span>
                        <span v-show="!formEditState"
                              class="label-span">{{getValue(item.value)}}</span>

                        <span v-if="formEditState">
                            <inputs v-if="item.style == 'input'" @getInput="getFrom" :ref="item.option.key"
                                    :option="item.option"></inputs>
                            <selects v-if="item.style == 'select'" @getSelect="getFrom" :ref="item.option.key"
                                     :option="item.option"></selects>
                            <datePicker v-if="item.style == 'timer' " @getDatePicker="getFrom" :ref="item.option.key"
                                   :dateOpt="item.option"></datePicker>
                        </span>
                    </li>
                </ul>
            </div>
            <div class="list-content detail-content list-contentB">
              <div class="list-header">
                <i class="hasIcon hasImg">
                  <img src="dist/img/costControl/price-icon.png" />
                </i>电价信息
              </div>

                <ul class="form-list detail-list" style="width: 60%;min-width: 55em; margin: 1em">
                    <table class="form-list table-list" :style="{width: width, 'font-size': '0.875em'}">
                        <tr class="table-header">
                            <!--<th style="width: 60px"></th>-->
                            <th style="width: 150px">电价项</th>
                            <th style="width: 150px">第一套</th>
                            <th style="width: 150px">第二套</th>
                        </tr>
                        <tr v-for="(item, $indexs) in solsItem" v-show="formEditState || item.select">
                            <!--<td><input v-show="formState()" type="checkbox" :checked="item.select" v-model="item.select"-->
                                       <!--alt="选择"/></td>-->
                            <td>{{item.name}}</td>
                            <td>
                                <span v-show="!formEditState">{{item.value1}}</span>
                                <input v-show="formEditState"  :disabled="!item.select"  type="text"   v-model="item.value1" style="background: #4C5E76"/>
                            </td>
                            <td>
                                <span v-show="!formEditState">{{item.value2}}</span>
                                <input v-show="formEditState"  :disabled="!item.select"  type="text"   v-model="item.value2"  style="background: #4C5E76"/>
                            </td>
                        </tr>
                    </table>
                </ul>
            </div>

        </VuePerfectScrollbar>
    </div>
</template>

<script>

    export default {
        data() {
            return {
                show :true,
                title: '电价方案信息',
                formEditState: false,
                formModel: 0, //0添加 1详细
                formData: {
                    priceSolsCode: '',      //电价方案代码
                    priceSolsName: '',      //方案名称
                    effectiveDate: '2017-07-24',    //方案实施时间
                    dictItemValue: '',    //方案应用的用户类型
                    solsRemark: '',       //方案备注
                    parameters: []        //方案的电价参数
                },
                solsItem: [
//                    {
//                        select: false,     //电价参数是否配置
//                        itemFlag: '040203', //参数项目标识
//                        name: '费率1',     //电价参数项名称
//                        firstPrice: '04020103', //电价参数项目第一套的标识
//                        twoPrice: '04020203',   //电价参数项目第二套的标识
//                        value1: '0.5224',       //电价参数项目第一套的值
//                        value2: '0.5224'        //电价参数项目第二套的值
//                   }
                ],
                width: ''
            }
        },
        computed: {
            alerts: function () {
                return this.$refs.alerts
            },
            list: function () {
                let width = '12em';
                return [
                  {
                    name: '方案编码',
                    value: this.formData.priceSolsCode,
                    isView: false,
                    style: 'input',
                    option: {
                      width: width,
                      key: 'formData.priceSolsCode',
                      value: this.formData.priceSolsCode,
                      type: 'disabledInput'}
                  },
                  {
                    name: '方案名称',
                    value: this.formData.priceSolsName,
                    isView: true,
                    style: 'input',
                    isNotNull: true,
                    option: {
                      width: width,
                      regular: "name",
                      errorMsg: "输入不合法,只能输入汉字、字母、数字！",
                      key: 'formData.priceSolsName',
                      value: this.formData.priceSolsName,
                      type: 'normalInput'
                    }
                  },
                  {
                    name: '实施时间',
                    value: this.formData.effectiveDate,
                    isView: true,
                    style: 'timer',
                    isNotNull: true,
                    option: {
                      width: width,
                      key: 'formData.effectiveDate',
                      value: this.formData.effectiveDate,
                      type: 'timer'
                    }
                  },
                  {
                    name: '计费类型',
                    value: this.formData.dictItemName || '',
                    style: 'select',
                    isView: true,
                    isNotNull: true,
                    option: {
                      key: 'formData.dictItemValue',
                      width: width,
                      cs: 'server',
                      constant: (this.formData.dictItemValue !== 'undefined' &&  this.formData.dictItemValue !== '') ?  {dictItemValue: this.formData.dictItemValue} : '',
                      method: 'post',
                      url: 'dictitem/query',
                      param: {dictCode: '1'},
                      rowName: 'data',
                      text: 'dictItemName',
                      value: 'dictItemValue'
                    }
                  },
                  {
                    name: '方案备注',
                    value: this.formData.solsRemark,
                    isView: true,
                    style: 'input',
                    option: {
                      width: width,
                      key: 'formData.solsRemark',
                      value: this.formData.solsRemark,
                      type: 'normalInput'
                    }
                  },
                  {
                    name: '方案状态',
                    value: this.formData.isEnabled === 1 ? '启用' : '禁用',
                    style: 'select',
                    isView: true,
                    option: {
                      key: 'formData.isEnabled',
                      width: width,
                      cs: 'client',
                      data: [{text: "启用", value: 1}, {text: "禁用", value: 0}],
                      constant: {value: this.formData.isEnabled || ''}
                    }
                  }]
            }
        },

        props: ['controls', 'row'],
        methods: {
            getFrom: function (val) {
              this.getOption(val, this)
            },
            resetComponent: function () {
                let that = this;
                that.show = false;
                that.$emit('closeDiv');
                setTimeout(function () {
                    that.show = false;
                }, 0)
            },
            formView: function (val) {
                let key = val.replace("formData.", "");
                if (key === 'isEnabled') {
                    if (this.formData[key] === 0) {
                        return '停用';
                    } else {
                        return '启用';
                    }
                }
                else if (key === 'dictItemValue') {
                    return this.formData['dictItemName'];
                }
                else {
                    return this.formData[key];
                }
            },
            //提交电价方案
            submit: function () {
                let that = this;
                //首先校验表单的数据
                this.regularTest({
                    emptyKey: ['formData.effectiveDate', 'formData.priceSolsName'],
                    callback: function () {
                        //参数
                        let solsparam = [];
                        let isReg = true; //校验费率电价的值
                        //添加方案值
                        that.solsItem.forEach(function (item) {
                            if (item.select) {
                                if (that.regularTestForVal({val: item.value1, regex: "float", msg: "第一套" + item.name})
                                    && that.regularTestForVal({
                                        val: item.value2,
                                        regex: "float",
                                        msg: "第一套" + item.name
                                    })) {
                                    solsparam.push({itemCode: item.firstPrice, dataValue: item.value1});
                                    solsparam.push({itemCode: item.twoPrice, dataValue: item.value2});
                                }
                                else {
                                    isReg = false;
                                    return;
                                }
                            }
                        });
                        if (isReg && solsparam.length === 0) {
                            that.alerts.centerDialog({msg: "请勾选电价方案！"});
                            return;
                        }
                        //数据校验成功
                        if (isReg) {
                            //提交数据
                            let param = that.formData;
                            param['parameters'] = solsparam;
                            //提交数据
                            that.getRequest({
                                url: 'price/addOrUpdate',
                                method: 'post',
                                param: param,
                                success: function (data) {
                                  that.alerts.show({msg: data.message});
                                  that.cancel();//取消编辑
                                },
                                error: function (data) {
                                  that.alerts.show({msg: data.message});
                                }
                            });
                        }
                    }
                });
            },
            //打开电价方案编辑窗口
            openEditClick: function () {
                this.formEditState =  !this.formEditState;
            },
            //获取电价项的标识值
            getPriceItemFlag: function (itemCode) {
                let reArr = itemCode.match(/^(\d{4})(\d{2})(\d{2})$/);
                return (reArr[1] + reArr[3]);
            },
            //获取电价参数项
            getPriceItem: function (itemFlag) {
                let priceItem = null;
                //电价方案参数
                this.solsItem.forEach(function (item) {
                    if (item.itemFlag === itemFlag) {
                        priceItem = item;
                        return true;
                    }
                });

                return priceItem;
            },
            //获取控件对象
            getContrlObj: function (ctrlKey) {
                let contrlObj = null;
                this.inputs.forEach(function (item) {
                    let keyObj = item.option.key.replace(/formData\./gi, '');
                    if (keyObj === ctrlKey) {
                        contrlObj = item;
                        return true;
                    }
                });
                return contrlObj;
            },
            //请求电价方案的详细信息
            queryDetail: function () {
                let that = this;
                //请求详细信息
                this.getRequest({
                    url: 'price/queryData',
                    method: 'post',
//                    method: 'get',
//                    url: 'dist/json/pricePlanDetail.json',
                    param: {
                        priceSolsCode: that.formData.priceSolsCode
                    },
                    success: function (data) {
                      that.formData = data.data; //方案详细信息
                      //formdata值赋值给电价方案参数对象
                      that.formData.parameters.forEach(function (item) {
                        let itemFlag = that.getPriceItemFlag(item.itemCode1);//获取电价参数标识
                        let itemSolsItem = that.getPriceItem(itemFlag);      //查询电价参数项
                        if (itemSolsItem !== null) {
                          itemSolsItem.value1 = item.dataValue1; //费率1
                          itemSolsItem.value2 = item.dataValue2; //费率2
                          itemSolsItem.select = true;
                        }
                      });
                      //将电价方案的详细信息值赋值给控件对象
                      for (let itemKey in that.formData) {
                        let contrlObj = that.getContrlObj(itemKey);
                        if (contrlObj !== null) {
                          let type = contrlObj.style;
                          switch (type) {
                            case 'select' :
                              contrlObj.option.constant[contrlObj.option.value] =  that.formData[itemKey];
                              break;
                            default:
                              contrlObj.option.value = that.formData[itemKey];
                              break;
                          }
                        }
                      }
                    //  console.log( that.formData)
                    },
                  error: function () {
                    that.alerts.show({msg: data.message});
                  }
                }, true)
            },
            //请求电价配置项
            queryPriceItem: function () {
                let that = this;
                this.getRequest({
                    url: 'price/queryParam',
                    method: 'post',
//                    url: 'dist/json/priceDetail.json',
//                    method: 'get',
                    success: function (data) {
                      //formdata值赋值给电价方案参数对象
                      data.data.forEach(function (item) {
                        that.solsItem.push({
                          select: false,     //电价参数是否配置
                          itemFlag: that.getPriceItemFlag(item.itemCode1), //参数项目标识
                          name: item.itemName1,        //电价参数项名称
                          firstPrice: item.itemCode1, //电价参数项目第一套的标识
                          twoPrice: item.itemCode2   //电价参数项目第二套的标识
                        });
                      });
                      if (that.formModel === 1) {
                        //请求电价方案的详细信息
                        that.queryDetail();
                      }
                    },
                  error: function () {
                    that.alerts.show({msg: data.message});
                  }
                })
            },
            init: function () {
                if (this.row !== '' && typeof this.row.priceSolsCode !== 'undefined') {
                    this.formData.priceSolsCode = this.row.priceSolsCode; //电价方案码
                    this.formModel = 1; //编辑界面
                }
                else {
                    this.formModel = 0; //添加界面
                }
                //初始化电价参数方案数据
                this.queryPriceItem();
            }
        },
        mounted: function () {
            this.isIn();
            this.init();
        }
    }
</script>

<style lang="scss" scoped>
    .EditorPrice {
        width: 100%;
        position: relative;
    }

    header {
        position: relative;
    }


</style>



