<template>
    <div ref="trys" style="height: calc(100% - 0em)">
        <div v-if="addOrInfo" class="info-container">
            <edit :controls="controls" :row="row" @closeDiv="closeDiv"></edit>
        </div>
        <div v-show="!addOrInfo" class="all-container">
          <div class="operate-div">
            <template v-for="(item, $index) in FormOpt">
              <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options"
                      @getInput="getFrom"></inputs>
              <selects ref="selects" style="margin-left:10px" v-if="item.type == 'select'" :option="item.options" @getSelect="getOption"></selects>
            </template>
          </div>
          <!--<div class="operate-div operateBtn">-->
            <!--&lt;!&ndash;v-if="controls[1]" v-if="controls[3]" &ndash;&gt;-->
            <!--<button class="button-operate" @click="addSolsClick">-->
              <!--<i class="hasIcon"><icon name="plus"></icon></i>添加方案-->
            <!--</button>-->
            <!--<button class="button-operate" @click="delSolsClick">-->
              <!--<i class="hasIcon"><icon name="trash"></icon></i>删除方案-->
            <!--</button>-->
            <!--<button class="button-operate" @click="enSolsClick">-->
              <!--<i class="hasIcon"><icon name="plus"></icon></i>启用方案-->
            <!--</button>-->
            <!--<button class="button-operate" @click="disSolsClick">-->
              <!--<i class="hasIcon"><icon name="trash"></icon></i>停用方案-->
            <!--</button>-->
          <!--</div>-->
          <!-- <div class="deepFilter" ref="deepFilter" v-if="deepSearchData.length > 0">
            <label>筛选条件：</label>
            <template v-for="(data, $index) in deepSearchData">
            <span class="deepSearchSpan">{{data.label}}： {{data.value}}
              <i v-if="data.flag" @click=" removeSearchData($index)">
                <icon name="remove" scale="0.8"></icon>
              </i>
            </span>
            </template>
          </div> -->
          <div ref="myOwnTable" class="table-container" :style="{height: tableHeight || 'calc(100% - 2.5em)'}">
            <tables ref="myTable" :tableOpt="TableOpt" class="tablePadding"
                   @rowDbClick="rowDbClick"></tables>
          </div>
        </div>
      <dialogs type="alert" ref="alerts"></dialogs>
    </div>
</template>
<script>
    import flash from  '../../../component/childComponent/FlashPlugin.vue'
    import edit from './EditorPrice.vue'
    export default {
        data() {
            return {
                searchParam: {
                  isEnabled: '' //方案类型
                },
                deepSearchData: '', //存储当前的筛选条件
                tableHeight: '',
                showTable: false,
                addOrInfo: false,
                controls: [],
                FormOpt: [
                    {
                    type: 'select',
                    options: {
                        key: 'searchParam.isEnabled',
                        cs: 'client',
                        width: '12em',
                        data: [{text: '全部方案', value: ''},
                            {text: '启用方案', value: '1'},
                            {text: '停用方案', value: '0'}]
                    }
                }],
                TableOpt: {
                    column: [
//                        {title: '选择', type: 'checkBox', textAlign: 'center', width: '40'},
                        {
                            title: '方案状态',
                            field: 'isEnabled',
                            textAlign: 'center',
                            width: '70',
                            type: 'string',
                            formatter: function (val, index, row, obj) {
                              return obj.$parent.getEnumItem('isUsed', val).text
                            }
                        },
                        {title: '方案代码', field: 'priceSolsCode', textAlign: 'center', width: '80'},
                        {title: '方案名称', field: 'priceSolsName', textAlign: 'center', width: '70'},
                        {title: '计费类型', field: 'dictItemName', textAlign: 'center', width: '80'},
                        {title: '实施时间', field: 'effectiveDate', textAlign: 'center', width: '80'},
                        {title: '方案描述', field: 'solsRemark', textAlign: 'center', width: '80'},
                        {title: '创建人员', field: 'createUserName', textAlign: 'center', width: '70'},
                        {title: '创建时间', field: 'createTime', textAlign: 'center', width: '70'},
                        {title: '更新人员', field: 'updateUserName', textAlign: 'center', width: '70'},
                        {title: '更新时间', field: 'updateTime', textAlign: 'center', width: '70'},
                    ],
                    url: 'price/queryList',
                    method: 'post',
//                    method: 'get',
//                    url: 'dist/json/pricePlan.json',
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20, 40, 60]
                }
            }
        },
        components: {
            flash,
            edit
        },
        methods: {
             removeSearchData: function (index) {
                let key = this.deepSearchData[index].key;
                if (key === 'isEnabled') {
                   this.$refs['selects'][0].constant =    {text: '全部方案', value: ''};
                   this.searchParam.isEnabled = '';
                 }
             },
            closeDiv: function () {
                this.addOrInfo = false;
                this.getParams();
            },

            //刷新表格数据
            getParams: function (pageNumber) {
              this.deepSearchData = [];
              let arr = this.getSearchData(this.searchParam);
              let labelName = '';
              for(let key in arr) {
                let value = arr[key];
                switch (key) {
                  case 'isEnabled':
                    labelName = '方案状态';
                    value = this.getEnumItem('isUsed', value).text;
                    break;
                }
                this.deepSearchData.push({label: labelName, value: value, key: key, flag: true})
              }
              this.getHeight();
              this.searchTable(arr, pageNumber);
            },

            //表格双击事件
            rowDbClick(items){
                //双击方案详情界面
             //   this.$router.push({path: '/Main/EditorPrice', query: {priceSolsCode: items.val.priceSolsCode}});
                this.row = {priceSolsCode: items.val.priceSolsCode};
                this.addOrInfo = true;
            },
            //添加电价方案
            addSolsClick: function () {
                //this.$router.push({path: '/Main/EditorPrice'});
                this.row = '';
                this.addOrInfo = true;
            },
            operateSols: function (confirmMsg, fun, param) {
              let that = this;
              that.getConfirm({
                emptyMsg: '请选择需要删除的电价方案!',
                confirmMsg: confirmMsg,
                callback: {
                  confirm: function () {
                    that[fun](param);
                  }
                }
              })
            },
            //删除电价方案
            delSolsClick: function () {
                this.operateSols('确定删除选择的电价方案','delSolsFunc');
            },
            //启用事件
            enSolsClick: function () {
              this.operateSols('确定启用选择的电价方案','solsDisFunc', 1);
            },
            //禁用事件
            disSolsClick: function () {
              this.operateSols('确定禁用选择的电价方案','solsDisFunc', 0);
            },

            //执行删除操作
            delSolsFunc: function () {
                let that = this;
                //请求详细信息
                this.getRequest({
                    url: 'price/delete',
                    method: 'post',
                    param: {
                        priceSolsCode: this.getSolsVal()
                    },
                    success: function (data) {
                        that.getParams(1); //刷新表格
                        that.alerts.show({msg: data.message});
                    },
                    error: function (data) {
                       that.alerts.show({msg: data.message});
                    }
                }, true);
            },
            //执行启用/禁用函数
            solsDisFunc: function (state) {
                let that = this;
                //请求详细信息
                this.getRequest({
                    url: 'price/modify',
                    method: 'post',
                    param: {
                        priceSolsCode: this.getSolsVal(),
                        isEnabled: state
                    },
                    success: function (data) {
                      that.getParams(1); //刷新表格
                      that.alerts.show({msg: data.message});
                    },
                    error: function () {
                       that.alerts.show({msg: data.message});
                     }
                }, true);
            },
            //获取表格中选择的行
            getSolsVal: function () {
                let items = this.$refs.myTable.getSelected();
                let selval = [];
                items.forEach(function (row) {
                    selval.push(row.priceSolsCode);
                });
                return selval.join(",");
            },
            windowChange: function () {
              let that = this;
              that.getHeight();
              /*window.onresize = function () {
                that.getHeight()
              }*/
            },
            init: function () {
                let that = this;
                /*window.onresize = function () {
                  that.windowChange();
                }*/
            }
        },
        computed: {
            alerts: function () {
                return this.$refs.alerts
            }
        },
        mounted: function () {
           this.isIn();
           this.getAction();
        },
        watch: {
          'searchParam.isEnabled': {
              handler() {
                  this.getParams(1);
              }
          }
        }
    }
</script>

<style lang="scss">
    @import "../../../assets/main.scss";
</style>



